body {
  color: #7c7c7c; /*文字色*/
  font-family: "M PLUS 1p";
}
.title {
  text-align: center;
  font-family: "Fredoka One", cursive;
  font-size: 280%;
  color: #f4cfe2;
}
.start-button-contents {
  padding: 5px;
  text-align: center;
  font-family: "B612 Mono", monospace;
}
button {
  margin: 5px;
  font-size: 50%;
  color: #7c7c7c; /*文字色*/
  border-radius: 0.5em;
}
.start {
  background-color: #f8bad7;
}
.end {
  background-color: #f8bad7;
}
.end {
  -webkit-animation: blink 1s ease-in-out infinite alternate;
  animation: blink 1s ease-in-out infinite alternate;
}
@-webkit-keyframes blink {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes blink {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.separate {
  content: "";
  display: block;
  clear: both;
}
.left-box {
  float: left;
  width: 30%;
  font-size: 20px;
}
.center-box {
  float: left;
  width: 45%;
  font-size: 20px;
  text-align: center;
}
.right-box {
  float: left;
  width: 25%;
  font-size: 20px;
}
.button-list {
  text-align: left;
  font-size: 15px;
  padding: 5px;
}
.grid-contener {
  display: grid; /* グリッドレイアウト */
  grid-template-columns: 25% 25% 25% 20%;
}
.form {
  margin: 2px;
}
.input-box-label {
  display: inline-block;
  width: 100%;
  font-size: 90%;
  text-align: center;
}
.select-category-label {
  display: inline-block;
  /* background-color: #fff; */
  width: 100%;
  font-size: 90%;
  text-align: center;
}
.select-contents-label {
  display: inline-block;
  /* background-color: #fff; */
  width: 100%;
  font-size: 90%;
  text-align: center;
}

.disctiption {
  font-size: 18px;
}
.input-text {
  padding: 0.5em 1em;
  width: 70%;
  height: 8em;
  font-weight: bold;
  color: #7c7c7c; /*文字色*/
  background: #fff;
  border: solid 3px #f8bad7; /*線*/
  border-radius: 10px; /*角の丸み*/
}
.input-text p {
  margin: 0;
  padding: 0;
  font-family: "B612 Mono", monospace;
}

.img {
  text-align: center;
}
